@font-face {
  font-family: "Campton-Book";
  src: url(./fonts/CamptonBold.otf);
}

body {
  background-color: #f2f2f2;
  font-family: "Campton-Book", "Open Sans";
}

h1 {
  font-size: 25px;
  font-weight: bold;
  /*margin-bottom: 50px;*/
}

h2 {
  font-size: 25px;
  color: #3b0078;
}

h5 {
  font-size: 15px;
  font-weight: bold;
  color: #c9c9c4;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.welcomeView {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 800px;
}

.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  z-index: 2;
}

.container img {
  justify-content: center;
}

.item {
  max-width: 20%;
}

.nameForm {
  background: transparent;
  border-width: 0px;
  border-bottom-width: 1px;
  min-width: 500px;
  font-size: 30px;
  text-align: center;
  margin-bottom: 25px;
}

.subtitles {
  display: flex;
  position: relative;
  justify-content: space-between;
  margin: auto;
  top: 6%;
}

.btn {
  border-radius: 50px;
  border-color: lightgrey;
  background-color: white;
  font-weight: bold;
  font-size: 15px;
  color: #9842f5;
}

.alert {
  margin-top: 20px;
}

.break {
  flex-basis: 100%;
  height: 0;
}

.imageContainer {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 20px;
  background-color: white;
  display: flex;
  justify-content: center;
  z-index: 3;
  box-shadow: 0px 3px 5px #888888;
}

.cardholder {
  width: 200px;
  height: 200px;
  border: dashed rgb(64, 247, 217) 2px;
  border-radius: 20px;
  background-color: transparent;
  display: flex;
  justify-content: center;
  z-index: 1;
}

.spacing {
  margin-top: 5%;
}

.winScreen {
  position: absolute;
  padding-top: 14.5%;
  height: 100%;
  width: 100%;
  text-align: center;
  z-index: 4;
  background-color: #f2f2f2;
}

.main {
  height: 100%;
  width: 100%;
}

.clock {
  margin-top: 0.5%;
  margin-left: 50%;
}

::placeholder {
  color: #e0e0de;
}
